.app{
  height: 100vh;

}

nav {
  width: full;
}

.nav-ul {
  padding: 5px;
  margin: 0;
  background-color: skyblue;
}

.nav-left {
  text-align: left;
  padding-left: 100px;
}

.logo {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  float: left;
  margin-top: 3px;
  margin-left: 20px;
}

.nav-ul li {
  text-decoration: none;
  display: inline-block;
  padding: 10px;
}

.nav-ul li a {
  color: black;
  text-decoration: none;
  padding: 5;
}

.footer {
  width: full;
  position: absolute;
  bottom: -50px;
  background-color: skyblue;
  width: 100%;
  text-align: center;
}

.parent {
  margin-left: 30%;
}

.inputBox {
  padding: 10px;
  display: block;
  border: 1px solid skyblue;
  width: 30vw;
  margin: 20px;
}

.appBtn {
  margin: 20px;
  width: 25vw;
  padding: 10px;
  background-color: skyblue;
  border: 1px solid;
  cursor: pointer;
}

.invalid-input {
  display: block;
  color: red;
  margin-top: -20px;
  margin-left: 20px;
}

.product-list {
  text-align: center;
  margin-top: 50px;
}

.product-list ul {
  margin: 0;
}

.product-list ul li {
  display: inline-block;
  width: 150px;
  border: 1px solid skyblue;
  padding: 5px;
}

.search-product-box {
  width: 40vw;
  height: 38px;
  margin: 20px;
  padding-left: 30px;
  border: 1px solid skyblue;
}
